.members-grid{
  margin: 2em 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  align-items: center;
  grid-gap: 1em;
  
  @include media-query("medium"){
    grid-template-columns: 1fr;
  }
}
.member-card{
    display: grid;
    grid-template-columns: 200px 1fr;
    width: 574px;

    @include media-query("large"){
      width: 472px;
    }

    @include media-query("medium"){
      width: 574px;
    }

    @include media-query("small-medium"){
      grid-template-columns: 120px 1fr;
      width: 343px;
    }
    background-color: $color-darkgrey;
    border-radius: 15px;
    overflow: hidden;
    
    figure.profile-picture{
      border-radius: 8px;
      width: 200px;
      height: 200px;
      @include media-query("small-medium"){
        width: 120px;
        height: 120px;
      }
      display: flex;
      justify-content: center;
      align-items: center;
      overflow: hidden;
      background-color: $color-darkgrey;
      img{
        width: auto;
        height: 100%;
      }
    }
  
    main{
     
      display: grid;
      grid-template-rows: 40px auto 40px;
      grid-gap: 1em;
      @include media-query("small"){
        grid-template-rows: 40px auto 32px;
        grid-gap: .25em;
      }

      header{
        padding: 1em;
        @include media-query("small"){
          padding: .5em .75em;
        }
      }

      p{
        padding: 0 1em;
        margin: 0;
        @include media-query("small"){
          padding: 0 .75em;
        }
      }
  
      footer{
        padding: 0 1em;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-end;
        @include media-query("small"){
          padding: 0 .75em;
        }
      }

      ul{
        margin: 0;
        padding: 0;
        list-style-type: none;
        li{
          display: inline-block;
        }
      }

      .icon{
        figure{
          margin: .25em;
        }
        img{
          @include media-query("small"){
            width: 24px;
            height: 24px;
          }
        }
      }
    }
}