.site-navigation{
  position: sticky;
  top: 0;
  left: 0;
  z-index: 9999;
}

div.main-navigation-bar {
  background-color: $color-darkblue;
}

.main-navigation-container {
  display: flex;
  flex-direction: column;
  align-items: center;

  width: 100%;

  position: relative;
}

.main-navigation-mobile{
  padding: 1em;
  margin-top: 44px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9998;
  
  width: 100%;
  height: 100%;
  background-color: $color-darkgrey;

  display: none;

  justify-content: center;
  align-items: center;

  -ms-overflow-style: none;
  scrollbar-width: none;

  @include media-query('medium') {
    display: flex;
  }

  &--close{
    display: none;
  }

  &__container{
    display: flex;
    justify-content: center;
    align-items: center;

    position: relative;
    width: 100%;
    height: 100%;
  }

  &__close{
    position: absolute;
    top: 0;
    right: 0;
    z-index: 9999;

    cursor: pointer;
  }
}

.main-navigation-online {
  width: 100%;
  background-color: $color-darkgrey;
  background-image: linear-gradient(to left, rgba(93, 52, 255),  rgba(93, 52, 255, .75));
  
    &__container{
      @include container-center;

      display: flex;
      flex-direction: row;
      justify-content: flex-start;
    }
}

.main-navigation-menu{
  padding-top: 1em;
  background-color: $color-darkblue;

  height: 70px;
  width: 100%;

  &__container{
    @include container-center;
    
    display: grid;
    grid-template-columns: 200px 1fr 120px;
    justify-content: flex-start;
    align-items: center;

    @include media-query('medium') {
      grid-template-columns: 1fr auto 120px;
    }
  }
}

nav.social-nav{
  li {
    margin: 0 4px;
    width: 44px;
    height: 44px;
    text-align: center;
  }
}

.ac-membersonline {
  width: auto;
  color: $color-lightgrey;
  font-weight: bold;
  line-height: 44px;

  display: grid;
  grid-template-columns: 16px 1fr;
  grid-gap: 8px;
  justify-content: center;
  align-items: center;

  &__indicator {
    margin: 4px;
    width: 12px;
    height: 12px;
    background-color: lightgreen;
    border-radius: 50%;
  }
}
