div.submenu-navigation-bar {
  position: sticky;
  top: 0;
  left: 0;
  z-index: 9998;

  display: flex;
  justify-content: center;

  border-bottom: 4px solid $color-primary;
}

.submenu-navigation-container {
  width: 100%;
}

.submenu-navigation-menu{
  background-color: $color-darkblue;

  display: flex;
  flex-direction: row;
  align-items: center;


  height: 60px;
  width: 100%;

  &__container{
    @include container-center;
    
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  nav{
    width: 100%;

    ul{
      display: flex;
      justify-content: space-between;
    }
  }
}
