.button {
  width: 240px;
  height: 44px;
  line-height: 44px;

  border-radius: 1.5em;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  font-weight: bold;

  position: relative;
  cursor: pointer;
  
  &.mobile-shrink {
    @include media-query('medium') {
      width: 120px;
    }
  }

  &:hover{
    &:after{
      content: "";
      position: absolute;
      top: 0;
      left: 0;

      width: 100%;
      height: 44px;

      background-color: rgba(255,255,255, .16);
      border-radius: 1.5em;
    }
  }

  figure{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  &__circle{
    width: 100%;
    max-width: 44px;
  }

  &__small{
    max-width: 120px;
  }

  &__large{
    max-width: 280px;
  }

  &__full{
    width: 100%;
    max-width: unset;
  }

  &--color-dark{
    background-color: $color-darkgrey;
    color: $color-lightgrey;
  }

  &--color-primary{
    background-color: $color-darkgrey;
    background-image: linear-gradient(to left, rgba(93, 52, 255),  rgba(93, 52, 255, .75));
    color: $color-lightgrey;
  }
  
  &--color-secondary{
    background-color: $color-secondary;
    color: $color-darkgrey;
  }

  &--border-white{
    background-color: transparent;
    color: $color-lightgrey;

    border: 2px solid $color-lightgrey;
  }

  &--color-transparent{
    background-color: transparent;
  }

  &--center{
    margin: 0 auto;
  }
}
