input[type="search"].form-input{
  margin: 0;
  padding-right: .75em;
  width: 100%;
  height: 44px;
  max-width: 320px;
  line-height: 44px;

  border-radius: 1.5em;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  font-weight: bold;

  position: relative;

  background-color: transparent;
  color: $color-lightgrey;
  border: 2px solid $color-lightgrey;

  text-indent: 1.5em;

  cursor: pointer;
  outline: 0;

  @include media-query('small') {
    max-width: unset;   
  }

  &::-webkit-search-cancel-button {
    -webkit-appearance: none;
    height: 1em;
    width: 1em;
    border-radius: 50em;
    background: url('../assets/icons/x--lightgrey.svg') no-repeat 50% 50%;
    background-size: contain;
    opacity: 0;
    pointer-events: none;
    filter: invert(1);
  }
  
  &:focus::-webkit-search-cancel-button {
    opacity: 1;
    pointer-events: all;
  }

}

fieldset.fieldset{
  all: unset;

  &--with-label{
    input{
      margin-top: .5em;
    }
  }
}


.form-select {
  position: relative;  
  float: left;
  min-width: 100%;

  position: relative;

  &:after {
    content: '';
    background-image: url('../assets/icons/chevron-down--lightgrey.svg');
    background-repeat: no-repeat;
    width: 1em;
    height: 1em;
    position: absolute;
    top: 12px;
    right: 1em;
  }

  select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;

    padding: 0px 24px;
    display: block;
    width: 100%;
    max-width: 320px;
    height: 44px;
    float: right;

    font-size: 1em;
    line-height: 44px;
    color: #333;
    background-color: transparent;
    background-image: none;
    -ms-word-break: normal;
    word-break: normal;
    border-radius: 1.5em;
  }

  &--naked{
    select{
      background: unset;
      border: unset;
      color: inherit;
    }
  }

  &--border-white{
    select{
      background-color: transparent;
      color: $color-lightgrey;
      font-weight: bold;
      border: 2px solid $color-lightgrey;
    }
  }
}

select::-ms-expand {
     display: none;
}


label{
  padding: .5em 0 1em;
}

button{
  all: unset;
}

.form-login{
  margin: 2em 0;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  grid-gap: 2em;

  width: 400px;

  @include media-query('small') {
    width: 320px;
  }

  .form-input,
  button.button{
    width: 100%;
  }
}

.form-input{
  padding: .125em 0.25em;
  outline: 0;
  cursor: pointer;

  &--with-label{
    margin-top: 1em;
  }

  &--default{
    display: block;
  
    height: 44px;
    min-width: 220px;

    border: 2px solid #C8C8C8;
    border-radius: 15px;

    text-indent: .75em;

    &:focus,
    &:active{
      border-color: $color-primary;
    }
  }
}

.form-checkbox{
  display: grid;
  grid-template-columns: 16px 1fr;

  &__description{
    margin-top: 0.25em;
  }
}