nav.menu-horizontal,
nav.menu-verticle {
  ul{
    margin: 0;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(6, auto);
    justify-content: flex-start;
    grid-gap: 2em;
  }

  li{
    color: $color-lightgrey;
    list-style-type: none;
    word-wrap: break-word;

    font-family: "Montserrat medium", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    font-weight: 500;
  }

  a > li{
    display: inline-flex;
    align-items: center;
  }

  a:hover{
    box-shadow: 0 2px 0 0 $color-link-active;
  }

  a.link-active{
    font-weight: bold;
    box-shadow: 0 2px 0 0 $color-link-active;
  }
}

nav.menu-verticle{
  ul{
    margin: 0 auto;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    justify-items: center;
  }
}