.education-module-slider{
  margin-top: -40px;

  @include media-query('medium') {
    margin-top: -40px;
  }
}

.education-module-sliders{
  margin-left: -1em;
  margin-right: -1em;
  padding-bottom: 80px;
  padding-top: 80px;

  @include media-query('large') {
    padding-bottom: 60px;
    padding-top: 60px;
  }

  @include media-query('small-medium') {
    padding-bottom: 40px;
    padding-top: 40px;
  }
}

.education-module-slide{
    margin-left: 1em;
    margin-right: 1em;
    border: 0;
    outline: 0;
}

.education-module-sliders > .slick-dots{
  margin: 0 1em;
  padding: 0;
  display: flex;
  justify-content: center;

  ul{
    margin: 0;
    padding: 0;
  }

  li{
    margin: .5em;
    display: inline-block;
 
    height: 32px;
    width: 32px;

    background-color: transparent;
    border: 2px solid $color-lightgrey;
    border-radius: 50%;

    cursor: pointer;

    button{
      width: 100%;
      height: 100%;
      text-align: center;
      font-size: 1em;
      font-weight: bold;
      line-height: 12px;
      color: $color-lightgrey;
    }
  }
  .slick-active{
    background-color: $color-primary;
    border-color: $color-primary;
  }
}