.content-article{
  display: grid;
  grid-template-columns: 200px 1fr;
  grid-gap: 1em;
  @include media-query("small"){
    grid-template-columns: 120px 1fr;
    grid-gap: .25em;
  }
  max-width: 574px;


  figure{
    border-radius: 8px;
    width: 200px;
    height: 200px;
    @include media-query("small"){
      width: 110px;
      height: 110px;
    }
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    background-color: $color-darkgrey;
    img{
      width: 100%;
    }
  }

  main{
    display: grid;
    grid-template-rows: 40px auto 24px;
    grid-gap: 1em;
    @include media-query("small"){
      grid-gap: .25m;
    }
    p{
      margin: 0;
    }

    footer{
      text-align: right;
    }
  }
}
