.testimonials{
  margin-top: -40px;

  @include media-query('medium') {
    margin-top: -40px;
  }
}

.testimonials-slider{
  margin-left: -1em;
  margin-right: -1em;
}

.testimonials-slide{
    border: 0;
    outline: 0;

    &__slide{
      margin: 1em;
      padding: 1em;
      background-color: white;
    }

  &__rating{
    margin: .25em 0;
    width: 100%;
    ul{
      margin: 0;
      padding: 0;
      display: grid;
      grid-template-columns: repeat(5, 24px);
      grid-gap: 10px;
      li{
      list-style-type: none;
      }
    }
  }
}

.slick-dots{
  margin: 0 1em;
  padding: 0;
  display: flex;
  justify-content: flex-end;

  ul{
    margin: 0;
    padding: 0;
  }

  li{
    margin: .5em;
    display: inline-block;
 
    height: 12px;
    width: 12px;

    background-color: #fff;
    border-radius: 50%;

    cursor: pointer;

    button{
      font-size: 0; 
    }
  }
  .slick-active{
    height: 10px;
    width: 10px;
    background-color: $color-primary;
  }
}